import Vue from 'vue'
import VueRouter from 'vue-router'
import Marry from '../views/Marry.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Index',
    component: Marry,
    meta: {
      title: 'Marry'
    }
  },
]

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  next();
})

export default router